
// e = 1 + 1/1 + 1/2 + 1/3! + ... + 1/n! + ...

export const e = (function () {
  let result = 1
  let denominator = 1
  for (let i = 1; i < 20; i++) {
    denominator = denominator * i
    result += 1 / denominator
  }
  return result
})()

