import {power} from './power.js'
import {sqrt} from './sqrt.js'

export function sd () {
  let mean = 0
  for (let i = 0; i < arguments.length; i++) {
    mean += arguments[i]
  }
  mean = mean / arguments.length

  let deviationSquare = 0 // init deviation_square (x-lambda)
  let sum = 0 // sum [(x-lambda)^2]
  for (let i = 0; i < arguments.length; i++) {
    deviationSquare = power(arguments[i] - mean, 2)
    sum += deviationSquare
  }

  // divide the sum of difference by number of inputs
  return sqrt(sum / arguments.length)
}



